import { Login } from '@/api';
export default {
  methods: {
    /**
     * user login
     */
    handleSubmit() {
      // this.$router.push('/admin/posts');
      const username = this.$refs.refUserName.value;
      const password = this.$refs.refPassword.value;
      Login.login({
        username,
        password
      }).then(() => {
        this.$router.push('/admin/posts');
      });
    }

  }
};